*,
*::before,
*::after {
   box-sizing: border-box;
}

.page {
   height: 100%;
   font-family: var(--font-family, sans-serif);
   -webkit-text-size-adjust: 100%;
   scroll-behavior: smooth;
   font-size: calc(100vw / 1920 * 16);
}

.page__body {
   margin: 0;
   min-width: 360px;
   min-height: 100%;
   background: #222222;
}

img {
   height: auto;
   max-width: 100%;
   object-fit: cover;
}

a {
   text-decoration: none;
}

.site-container {
   overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
   min-height: 100vh;
   display: grid;
   grid-template-columns: 75.3125% 1fr;
   color: var(--light-color);
}
.site-wrap {
   position: relative;
}

.is-hidden {
   display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
   border: none;
   padding: 0;
   background-color: transparent;
   cursor: pointer;
}

.list-reset {
   list-style: none;
   margin: 0;
   padding: 0;
}

.input-reset {
   -webkit-appearance: none;
   appearance: none;
   border: none;
   border-radius: 0;
   background-color: #fff;

   &::-webkit-search-decoration,
   &::-webkit-search-cancel-button,
   &::-webkit-search-results-button,
   &::-webkit-search-results-decoration {
      display: none;
   }
}

.visually-hidden {
   position: absolute;
   overflow: hidden;
   margin: -1px;
   border: 0;
   padding: 0;
   width: 1px;
   height: 1px;
   clip: rect(0 0 0 0);
}

.container {
   margin: rem(20);
   max-width: var(--container-width);
}

.centered {
   text-align: center;
}

.dis-scroll {
   position: fixed;
   left: 0;
   top: 0;
   overflow: hidden;
   width: 100%;
   height: 100vh;
   overscroll-behavior: none;
}

.page--ios .dis-scroll {
   position: relative;
}
